$(function() {
  // header. fixed top position
  // https://github.com/innoq/stickynav
  new StickyNav('.header');

  // services. show hidden items
  $('.services__btn').click(function (event) {
    event.stopPropagation();
    event.preventDefault();
    $(this).toggleClass('services__btn_hidden');
    $('.services .container._hidden').toggleClass('_hidden');
  });

  // map
  let windowWidth = $(window).outerWidth();
  if (windowWidth >= 992) {
    ymaps.ready(initDesktopMap);
    // https://github.com/inuyaksa/jquery.nicescroll
    $('.statistics').niceScroll();
  }
  else {
    ymaps.ready(initMobileMap);
    // statistics scroll
    // https://kenwheeler.github.io/slick/
    $('.statistics .row').slick({
      infinite: false,
      slidesToShow: 3,
      slidesToScroll: 2,
      dots: false,
      arrows: false,
      variableWidth: true
    });
    // $('.header__list').niceScroll({
    //   zindex: 2,
    //   cursorcolor: 'transparent',
    //   cursorwidth: '7px',
    //   cursorborder: "1px solid transparent",
    //   emulatetouch: true,
    //   // autohidemode: false,
    //   autohidemode: "cursor",
    //   cursorborderradius: 3.5,
    //   railpadding: { top: 0, right: 0, left: 0, bottom: 0 },
    //   enabletranslate3d: false
    // });
  }

  function initDesktopMap () {
    // main (desktop) page
    let myMap;
    myMap = new ymaps.Map('map-index-page', {
      center: [55.744840751099204,37.63296976579277],
      zoom: 15,
      controls: []
    });

    let myPlacemark = new ymaps.Placemark([55.7445865689778,37.64362349999996], null,{
    	iconLayout: 'default#image',
    	iconImageHref: '../img/map/marker.svg',
    	iconImageSize: [43,59],
    	iconImageOffset: [-21.5, -59]
    });
    myMap.geoObjects.add(myPlacemark);
  }

  function initMobileMap () {
    // main page mobile
    let myMap;
    myMap = new ymaps.Map('map-index-page', {
      center: [55.7445865689778,37.64362349999996],
      zoom: 15,
      controls: []
    });

    let myPlacemark = new ymaps.Placemark([55.7445865689778,37.64362349999996], null,{
    	iconLayout: 'default#image',
    	iconImageHref: '../img/map/marker.svg',
    	iconImageSize: [43,59],
    	iconImageOffset: [-21.5, -59]
    });
    myMap.geoObjects.add(myPlacemark);
  }

  // other sites. show/hide tabs
  $('.header__select-title').on('click', function(event) {
    event.stopPropagation();
    event.preventDefault();
    $(this).toggleClass('header__select-title_active')
    $('.other-sites').toggleClass('other-sites_active')
  });

  // click outside the window (other sites)
  $(document).mouseup(function (e) {
    let div = $('.header');
    let otherSites = $('.other-sites');
    let selectTitle = $('.header__select-title');
    if (!div.is(e.target) && div.has(e.target).length === 0) {
      otherSites.removeClass('other-sites_active');
      selectTitle.removeClass('header__select-title_active');
    }
  });

  // other sites. tabs toggle
  $('.other-sites__tabs').on('click', '.other-sites__tabs-item:not(.other-sites__tabs-item_active)', function() {
    $(this)
      .addClass('other-sites__tabs-item_active')
      .siblings()
      .removeClass('other-sites__tabs-item_active')
      .closest('.other-sites')
      .find('.other-sites__content-item')
      .removeClass('other-sites__content-item_active')
      .eq($(this).index())
      .addClass('other-sites__content-item_active');
  });

  // search desktop. show/hide
  let $searchLink = $('.header__link.header__link_search');
  let $headerNav = $('.header__nav');
  let $searchWrapper = $('.header__search-wrapper');
  let $search = $('.search');
  let $searchClose = $('.search__close');
  let $headerMobileMenuBtn = $('.header-mobile__menu-btn');
  $searchLink.click(function () {
    $searchLink.addClass('header__link_hidden');
    $headerNav.addClass('header__nav_hidden');
    $search.addClass('search_active');
    $searchWrapper.addClass('header__search-wrapper_active');
    $headerMobileMenuBtn.removeClass('header-mobile__menu-btn_active')
    // $search.animate({width: '100px', marginLeft: 100}, {duration: 500});
    // search. scrollbar
    // https://github.com/inuyaksa/jquery.nicescroll
    function initScrollbar () {
      $('.search__autocomplete').niceScroll({
        zindex: 2,
        cursorcolor: '#DADADA',
        cursorwidth: '7px',
        cursorborder: "1px solid transparent",
        emulatetouch: true,
        // autohidemode: false,
        autohidemode: "cursor",
        cursorborderradius: 3.5,
        railpadding: { top: 0, right: 0, left: 0, bottom: 0 },
        enabletranslate3d: false
      });
      $('body').toggleClass('body_hidden');
    }
    setTimeout(initScrollbar, 300);
  });
  $searchClose.click(function () {
    $searchLink.removeClass('header__link_hidden');
    $headerNav.removeClass('header__nav_hidden');
    $search.removeClass('search_active');
    $searchWrapper.removeClass('header__search-wrapper_active');
    $('.search__autocomplete').getNiceScroll().remove();
    $('body').toggleClass('body_hidden');
  });

  // search mobile. show/hide
  let $searchMobileLink = $('.header-mobile__search');
  let $searchMobileLink2 = $('.nav-mobile__link_search');
  let $searchMobile = $('.search-mobile');
  let $searchMobileClose = $('.search-mobile__close');
  let $navMobile = $('.nav-mobile');
  function initSearchMobile () {
    $searchMobile.addClass('search-mobile_active');
    $navMobile.removeClass('nav-mobile_active');
    $headerMobileMenuBtn.removeClass('header-mobile__menu-btn_active');
    // search. scrollbar
    // https://github.com/inuyaksa/jquery.nicescroll
    function initScrollbar () {
      $('.search-mobile__autocomplete').niceScroll({
        cursorcolor: '#DADADA',
        cursorwidth: '7px',
        cursorborder: '1px solid transparent',
        emulatetouch: true,
        // autohidemode: false,
        autohidemode: "cursor",
        cursorborderradius: 3.5,
        railpadding: { top: 0, right: 0, left: 0, bottom: 0 },
        enabletranslate3d: false
      });
    }
    setTimeout(initScrollbar, 200);
  }
  $searchMobileLink.click(function () {
    initSearchMobile();
  });
  $searchMobileLink2.click(function () {
    initSearchMobile();
  });
  $searchMobileClose.click(function () {
    $searchMobile.removeClass('search-mobile_active');
    $('.search__autocomplete').getNiceScroll().remove();
  });

  // search universal
  let $searchBtnDefault = $('.search-btn-default');
  let $searchAutocompleteDefault = $('.search-autocomplete-default');
  $searchBtnDefault.click(function () {
    $searchAutocompleteDefault.toggleClass('search-autocomplete-default_active');
    // search. scrollbar
    // https://github.com/inuyaksa/jquery.nicescroll
    function initScrollbar () {
      $searchAutocompleteDefault.niceScroll({
        zindex: 3,
        cursorcolor: '#DADADA',
        cursorwidth: '7px',
        cursorborder: "1px solid transparent",
        emulatetouch: true,
        // autohidemode: false,
        autohidemode: "cursor",
        cursorborderradius: 3.5,
        railpadding: { top: 0, right: 0, left: 0, bottom: 0 },
        enabletranslate3d: false
      });
      $('body').toggleClass('body_hidden');
    }
    setTimeout(initScrollbar, 200);
  });

  // click outside the window (search default)
  $(document).mouseup(function (e) {
    let div = $('.centers__search');
    let searchDefault = $searchAutocompleteDefault;
    // let otherSites = $('.other-sites');
    // let selectTitle = $('.header__select-title');
    if (!div.is(e.target) && div.has(e.target).length === 0) {
      searchDefault.removeClass('search-autocomplete-default_active');
      // selectTitle.removeClass('header__select-title_active');
    }
  });

  // mobile navigation
  $('.header-mobile__menu-btn').click(function (){
		$(this).toggleClass('header-mobile__menu-btn_active');
		$('.nav-mobile').toggleClass('nav-mobile_active');
	});

  // mobile navigation other sites
  $('.nav-mobile__link.nav-mobile__link_other-sites').click(function () {
    $('.nav-mobile__list_primary').toggleClass('nav-mobile__list_hidden');
    $('.nav-mobile__list_other-sites').toggleClass('nav-mobile__list_active');
  });

  $('.nav-mobile__list-item-title').click(function () {
    $(this).next().toggleClass('nav-mobile__list-item-content_active');
    $(this).closest('.nav-mobile__list-item').toggleClass('nav-mobile__list-item_active');
  });
  $('.nav-mobile__link.nav-mobile__link_back').click(function () {
    $('.nav-mobile__list_primary').toggleClass('nav-mobile__list_hidden');
    $('.nav-mobile__list_other-sites').toggleClass('nav-mobile__list_active');
  });

  // text-info mobile slider
  // https://kenwheeler.github.io/slick/
  let $slickSlider = $('.text-info');
  let settingsSlider = {
    dots: true,
    arrows: false
  }
  slickOnMobile($slickSlider, settingsSlider);

  function slickOnMobile(slider, settings){
    $(window).on('load resize', function() {
      if ($(window).width() > 992) {
        if (slider.hasClass('slick-initialized')) {
          slider.slick('unslick');
        }
        return
      }
      if (!slider.hasClass('slick-initialized')) {
        return slider.slick(settings);
      }
    });
  };

  // select
  // https://selectric.js.org
  // select. scrollbar
  // https://github.com/inuyaksa/jquery.nicescroll
  function initScrollbarSelect () {
    $('.selectric-scroll').niceScroll({
      zindex: 3,
      cursorcolor: '#DADADA',
      cursorwidth: '7px',
      cursorborder: "1px solid transparent",
      emulatetouch: true,
      // autohidemode: false,
      autohidemode: "cursor",
      cursorborderradius: 3.5,
      railpadding: { top: 0, right: 0, left: 0, bottom: 0 },
      enabletranslate3d: false
    });
    // $('body').toggleClass('body_hidden');
  }
  $('.select-default').selectric({
    disableOnMobile: false,
    nativeOnMobile: false,
    maxHeight: 366,
    onInit: function() {
      setTimeout(initScrollbarSelect, 300);
    },
    labelBuilder: function(currItem) {
      return (currItem.value.length ? '<span class="select-default__icon-' + currItem.value + '"></span>' : '') + '<span>' + currItem.text + '</span>';
    }
  });

  // input text. disable all but numbers
  function showOnlyNumbers(element) {
    $(element).keypress(function (e) {
      if (e.which != 8 && e.which != 0 && (e.which < 48 || e.which > 57)) {
        return false;
      }
    });
  }
  showOnlyNumbers('.pagination__input-text');

  // input text. resize
  function resizeInput() {
    $(this).attr('size', $(this).val().length);
  }
  $('.pagination__input-text').keyup(resizeInput).each(resizeInput);

});
